:root {
  --color-black: #000;
  --color-white: #fff;
  --color-dark: #151515;
  --color-quill-gray: #D9D9D7;
  --color-gray: #858585;
  --color-tundora: #454545;
}

@font-face {
  font-family: "Jost-Bold";
  src: url("../fonts/Jost-Bold.woff2") format("woff2"), url("../Jost-Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Jost-Medium";
  src: url("../fonts/Jost-Medium.woff2") format("woff2"), url("../Jost-Medium.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

