html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-weight: 500;
  font-family: "Jost", sans-serif;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-link {
  position: relative;
  font-size: 15px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 0%;
  height: 2px;
  background-color: currentColor;
  transition: width .3s;
}

.main-link:hover::before {
  width: 100%;
  transition: width .3s;
}

.main-link--white {
  color: var(--color-white);
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 48px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

@font-face {
  font-family: 'swiper-icons';
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}

:root {
  --swiper-theme-color: #007aff;
}

.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto;
}

.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */
.swiper-container-3d {
  perspective: 1200px;
}

.swiper-container-3d .swiper-wrapper,
.swiper-container-3d .swiper-slide,
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom,
.swiper-container-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-container-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

:root {
  /*
  --swiper-pagination-color: var(--swiper-theme-color);
  */
}

.swiper-pagination {
  position: absolute;
  text-align: center;
  transition: 300ms opacity;
  transform: translate3d(0, 0, 0);
  z-index: 10;
}

.swiper-pagination.swiper-pagination-hidden {
  opacity: 0;
}

/* Common Styles */
.swiper-pagination-fraction,
.swiper-pagination-custom,
.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 10px;
  left: 0;
  width: 100%;
}

/* Bullets */
.swiper-pagination-bullets-dynamic {
  overflow: hidden;
  font-size: 0;
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  transform: scale(0.33);
  position: relative;
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active {
  transform: scale(1);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-main {
  transform: scale(1);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev {
  transform: scale(0.66);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev-prev {
  transform: scale(0.33);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next {
  transform: scale(0.66);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next-next {
  transform: scale(0.33);
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 100%;
  background: #000;
  opacity: 0.2;
}

button.swiper-pagination-bullet {
  border: none;
  margin: 0;
  padding: 0;
  box-shadow: none;
  appearance: none;
}

.swiper-pagination-clickable .swiper-pagination-bullet {
  cursor: pointer;
}

.swiper-pagination-bullet-active {
  opacity: 1;
  background: var(--swiper-pagination-color, var(--swiper-theme-color));
}

.swiper-container-vertical > .swiper-pagination-bullets {
  right: 10px;
  top: 50%;
  transform: translate3d(0px, -50%, 0);
}

.swiper-container-vertical > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 6px 0;
  display: block;
}

.swiper-container-vertical > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic {
  top: 50%;
  transform: translateY(-50%);
  width: 8px;
}

.swiper-container-vertical > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  display: inline-block;
  transition: 200ms transform, 200ms top;
}

.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 4px;
}

.swiper-container-horizontal > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic {
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
}

.swiper-container-horizontal > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  transition: 200ms transform, 200ms left;
}

.swiper-container-horizontal.swiper-container-rtl > .swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  transition: 200ms transform, 200ms right;
}

/* Progress */
.swiper-pagination-progressbar {
  background: rgba(0, 0, 0, 0.25);
  position: absolute;
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background: var(--swiper-pagination-color, var(--swiper-theme-color));
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transform: scale(0);
  transform-origin: left top;
}

.swiper-container-rtl .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  transform-origin: right top;
}

.swiper-container-horizontal > .swiper-pagination-progressbar,
.swiper-container-vertical > .swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
  width: 100%;
  height: 4px;
  left: 0;
  top: 0;
}

.swiper-container-vertical > .swiper-pagination-progressbar,
.swiper-container-horizontal > .swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
  width: 4px;
  height: 100%;
  left: 0;
  top: 0;
}

.swiper-pagination-white {
  --swiper-pagination-color: #ffffff;
}

.swiper-pagination-black {
  --swiper-pagination-color: #000000;
}

.swiper-pagination-lock {
  display: none;
}

.header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 100;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 39px;
  padding-bottom: 39px;
}

.header .logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.header .logo svg {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  fill: var(--color-dark);
}

.header__shop-nav {
  margin-left: 250px;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 45px;
}

.nav__link--current::before {
  width: 100%;
  transition: width .3s;
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__item:not(:last-child) {
  margin-right: 30px;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.shop-nav__link--user {
  background-image: url("../../img/user.png");
}

.shop-nav .cart {
  width: auto;
  height: auto;
}

.shop-nav .cart__icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../../img/basket.png");
}

.shop-nav .cart__quantity {
  display: inline-flex;
  align-items: center;
  font-size: 10px;
  width: 16px;
  height: 16px;
  color: var(--color-white);
  font-weight: 700;
  padding: 0 5px;
  background-color: var(--color-dark);
  border-radius: 25px;
}

.shop-nav .search-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../../img/loup.png");
}

.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: var(--color-quill-gray);
}

.hero::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(var(--color-black), 0.05);
}

.hero__container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 150px;
}

.hero__title {
  max-width: 560px;
  font-size: 72px;
  line-height: 1.2;
  font-weight: 700;
  margin: 0 0 40px;
}

.hero__descr {
  margin: 0;
  font-size: 18px;
}

.hero__images {
  position: absolute;
  max-height: 90%;
  right: 100px;
  bottom: 0;
}

.hero .swiper-container {
  position: relative;
  height: 100%;
  width: 100%;
}

.hero .swiper-slide {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.hero .banner-pag {
  position: absolute;
  display: flex;
  justify-content: center;
  max-width: 300px;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.hero .banner-pag .swiper-pagination-bullet {
  margin: 0 12px;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.hero .banner-pag .swiper-pagination-bullet-active {
  position: relative;
  opacity: 1;
  background-color: var(--color-dark);
}

.hero .banner-pag .swiper-pagination-bullet-active:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background-color: var(--color-white);
  z-index: -1;
}

.hero .marketing {
  position: absolute;
  display: flex;
  width: 280px;
  left: 40px;
  bottom: 50px;
  padding: 15px;
  background-color: var(--color-white);
  box-shadow: 0 5px 30px 0 rgba(0, 0, 0, 0.1);
  z-index: 10;
  transform: translateX(-150vw);
  transition: transform 1s;
}

.hero .marketing--visible {
  transform: translateX(0);
}

.hero .marketing__image {
  margin-right: 15px;
  width: 115px;
}

.hero .marketing__image img {
  object-fit: cover;
}

.hero .marketing__descr {
  margin-bottom: 9px;
  font-size: 11px;
  color: var(--color-gray);
}

.hero .marketing__title {
  min-height: 34px;
  margin-bottom: 7px;
  font-size: 15px;
  color: var(--color-dark);
}

.hero .marketing__when-from {
  padding-right: 10px;
  display: block;
  font-size: 11px;
  font-style: italic;
  color: var(--color-gray);
}

.hero .marketing__close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 10px;
  height: 10px;
  background-image: url("../../img/close.png");
  background-repeat: no-repeat;
  background-position: center;
}

.hero .marketing__view {
  position: absolute;
  bottom: 10px;
  right: 11px;
  width: 13px;
  height: 9px;
  background-image: url("../../img/eye.png");
  background-color: transparent;
  border: none;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.banners {
  padding-top: 50px;
  padding-bottom: 100px;
}

.banners__list {
  display: flex;
}

.banners__item {
  width: 33%;
  flex-shrink: 0;
}

.banners__item:not(:last-child) {
  margin-right: 0.6%;
}

.banners-article {
  min-height: 290px;
  padding: 60px;
  background-color: #cfd1d0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.banners-article__title {
  padding-right: 40%;
  margin-bottom: 20px;
  font-size: 36px;
}

.banners-article--midseasons {
  background-image: url("../../img/midseasons.jpg");
}

.banners-article--summer {
  background-image: url("../../img/summer.jpg");
}

.banners-article--access {
  background-image: url("../../img/access.jpg");
}

.main-products {
  padding: 55px 0;
  /* arrow-down */
  /* products-grid */
}

.main-products__nav {
  display: flex;
  justify-content: center;
  margin-bottom: 45px;
}

.main-products__item + .main-products__item {
  margin-left: 30px;
}

.main-products__button {
  font-size: 24px;
  color: var(--color-gray);
}

.main-products__button--current {
  color: var(--color-dark);
}

.main-products__button--current::before {
  width: 100%;
  transition: width .3s;
}

.main-products__button:hover {
  color: var(--color-dark);
}

.main-products__btn-center {
  margin-top: 45px;
  text-align: center;
}

.main-products__more {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  font-size: 13px;
  font-weight: 500;
  color: var(--color-gray);
}

.main-products__more svg {
  width: 13px;
  height: 22px;
  fill: var(--color-dark);
  margin-top: 13px;
}

.main-products .products-grid {
  display: flex;
  flex-wrap: wrap;
}

.main-products .products-grid__item {
  flex-basis: 19%;
}

.main-products .products-grid__item:not(:nth-child(5n)) {
  margin-right: 15px;
}

.main-products .products-grid__item:nth-child(-n+5) {
  margin-bottom: 40px;
}

.product {
  position: relative;
}

.product__image {
  margin-bottom: 20px;
}

.product__image img {
  object-fit: cover;
  max-height: 100%;
}

.product__title {
  font-size: 13px;
  margin-bottom: 11px;
}

.product__title a::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.product__price {
  font-size: 13px;
  line-height: 1.2;
}

.shop-banners {
  padding-top: 45px;
}

.shop-banners__container {
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.shop-banner {
  position: relative;
  width: 50%;
  min-height: 420px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 160px 15px;
  color: var(--color-white);
  background-color: var(--color-dark);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.shop-banner::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(215, 8, 8, 0.1);
}

.shop-banner--men {
  background-image: url("../../img/banner_1.jpg");
}

.shop-banner--woman {
  background-image: url("../../img/banner_2.jpg");
}

.shop-banner__title {
  position: relative;
  font-size: 48px;
  font-weight: 500;
  margin-bottom: 20px;
  z-index: 1;
}

.shop-banner__text {
  position: relative;
  z-index: 1;
}

.footer__content {
  padding: 96px 50px 0;
}

.footer__top {
  display: flex;
  justify-content: space-between;
  padding-bottom: 100px;
}

.footer__column:nth-child(2) {
  margin-left: -60px;
}

.footer__title {
  margin: 0 0 37px;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}

.footer__item + .footer__item {
  margin-top: 18px;
}

.footer__list--row {
  display: flex;
}

.footer__list--row .footer__item {
  margin: 0 40px 0 0;
}

.footer__list--row .footer__item:last-child {
  margin-right: 0;
}

.footer__link {
  font-size: 13px;
  color: var(--color-tundora);
}

.footer__form {
  width: 650px;
  margin-bottom: 20px;
}

.footer__text {
  font-size: 13px;
  font-weight: 700;
}

.footer__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32px 0;
  font-size: 13px;
  font-weight: 700;
  border-top: 1px solid #EEE;
}

.footer__social {
  margin-right: 17px;
}

/* SUBSCRIBE FORM */
.subscribe-form__title {
  font-size: 13px;
  font-weight: 500;
  color: var(--color-tundora);
  margin-bottom: 40px;
}

.subscribe-form__field {
  position: relative;
  display: block;
  width: 100%;
}

.subscribe-form__field input {
  width: 100%;
  padding: 0 100px 9px 0;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
  border: none;
  border-bottom: 2px solid var(--color-dark);
  outline: none;
}

.subscribe-form__field input::placeholder {
  color: rgba(69, 69, 69, 0.5);
  font-size: 13px;
  font-weight: 500;
}

.subscribe-form__btn {
  position: absolute;
  right: 16px;
  bottom: 40%;
  font-family: "Jost", sans-serif;
  font-size: 13px;
  line-height: 1.2;
  font-weight: 700;
  color: var(--color-dark);
}

/* SOCIAL BUTTONS */
.social {
  display: flex;
}

.social__item + .social__item {
  margin-left: 30px;
}

.social__link {
  display: block;
}

.social__link svg {
  width: 16px;
  height: 16px;
}
